.footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 50px;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  
  grid-column: 1 / -1;
}

@media( min-width: 760px) {
  .footer {
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: start;
        align-items: flex-start; 
    -ms-flex-pack: distribute; 
        justify-content: space-around;
  }
}

.footer__figure {
  margin: 0;
}

.footer__img {
  width: 150px;
  margin-top:-10px;
}

.footer__list {
  list-style: none;
  padding: 0;
  margin: 0 0 30px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

@media( min-width: 760px) {
  .footer__list {
    -ms-flex-align: baseline;
        align-items: baseline;
    -ms-flex-pack: end;
        justify-content: flex-end;
    margin-bottom: 0;
  }
}

.footer__list__title {
  margin: 0;
  /* color: var(--dark-primary-color); */
  color:#47525E;
  font-weight: 300 !important;
  font: 500 1em 'Roboto', sans-serif;
  text-decoration: none;
}

.footer__list__item {
  margin-top: 5px;
  padding: 5px;
}

.footer__list__link {
  text-decoration: none;
  color: var(--secondary-color);
  font: 300 14px 'Roboto', sans-serif;
}

.footer__title {
  margin-top: 10px;
  color: var(--dark-primary-color);
  font: 500 1em 'Roboto', sans-serif;
}

.footer__list__social-network {
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer__list__item__social-network {
  margin: 5px 5px 0 0;
}

.footer__list__wwcode {
  margin: 0px 0 0 10px;
  color: var(--dark-primary-color);
  font: 500 1em 'Roboto', sans-serif;
}



