.menu-container {
	position: relative;
}

.menu__button {
	background: #ff50cd;
	color: var(--light-secondary-color);
	border: 0;
	padding: 10px 20px;
	font-size: 16px;
	cursor: pointer;
	display: block;
	position: absolute;
	right: 20px;
	top: 20px;
	text-align: center;
}

.menu{
		width: 100vw;
		height: 100vh;
		background: rgba(206, 161, 255, .5);
		display: -ms-flexbox;
		display: flex;
		-ms-flex-direction: column-reverse;
		    flex-direction: column-reverse;
		-ms-flex-pack: end;
		    justify-content: flex-end;
		-ms-flex-align: center;
		    align-items: center;
		padding-top: 50px;
}

@media (min-width: 890px){
  .menu {
		width: 100%;
		height: 100%;
		background: none;
		-ms-flex-direction: row;
		    flex-direction: row;
		padding: 0;
		-ms-flex-align: start;
		    align-items: flex-start;
		-ms-flex-pack: justify;
		    justify-content: space-between;
	}
}

.menu__figure {
	margin: 0;
}

.menu__img {
	-o-object-fit: contain;
	   object-fit: contain;
}

.menu--show-menu {
	display: none;
}

@media (min-width: 890px){
  .menu--show-menu {
		display: -ms-flexbox;
		display: flex;
	}
}

@media (min-width: 890px){
  .menu__button {
		display: none;
	}
}

.menu__list {
	margin: 10px 0 0 0;
	padding: 0;
	max-width: 650px;
	width: 100%;
	list-style: none;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	
}

@media (min-width: 890px){
  .menu__list {
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-pack: end;
		    justify-content: flex-end;
		-ms-flex-direction: row;
		    flex-direction: row;
	}
}

.menu__list__item {
	margin: 10px;
}

.menu__list__link {
	text-decoration: none;
	color: var(--light-secondary-color);
	text-transform: uppercase;
	font-weight: 300;
	text-align: center;
	font-size: 14px;
	padding: 10px;
	width: 100%;
}

.menu__list__link:hover {
	color: #cea1ff;
	border-bottom: 2px solid #ff50cd; 
}



